@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Italianno&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
/* 
@import url('https://fonts.googleapis.com/css2?family=Italianno&display=swap'); */



:root{
  --primary-white: #fff;
  --primary-crimson:#DC143C;
  --primary-light-dark:#303030;
  --primary-dark:rgba(255,255,255, 0);
  --primary-black:#000000;
  --primary-red:#640000;
  --primary-alicewhite:#F0F8FF;
  --primary-light-grey:#d3d3d3;
  --primary-grey:#808080;
  --primary-green:green;
  --primary-green2:#e3fcea;
  
}
body{
  font-family: 'Share Tech Mono', monospace !important;
}
/* navbar */
nav{
  z-index: 2;
}
/* Modify the backgorund color */ 
.navbar-custom { 
	background-color:#021933; 
  padding: 1.3rem 0;
} 



/* menu link */
nav a.nav-link  {
  /* font-weight: 500; */
  letter-spacing: 0.6px;
  cursor: pointer;
}
nav ul li{
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem  solid rgba(255,255,255, 0);
  border-top: 0.1875rem  solid var(--primary-dark);
  
  font-family: 'Share Tech Mono', monospace;

}

.navbar-brand{
  font-family: 'Italianno', cursive;
  
  font-size: xx-large !important;
  color: #fff !important;
  color: var(--primary-white) !important;
  padding: 0 !important;
}
.navbar-toggler{
 border: 1px solid #DC143C !important;
 border: 1px solid var(--primary-crimson) !important;
  
  
}

@media(max-width: 768px){
  .navbar-brand {
     margin-top: 0rem !important;
  }
  .navbar-nav > li > a {
    padding: auto;
    margin-right: 0.55rem;
    margin-left: 0.55rem;
    color: #fff !important;
    color: var(--primary-white) !important;
    border-top: 0.1875rem  solid rgba(255,255,255, 0);
    border-top: 0.1875rem  solid var(--primary-dark);
    font-family: 'Share Tech Mono', monospace;
    
  
  }


}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus{
  border-top: 0.1875rem solid #DC143C;
  border-top: 0.1875rem solid var(--primary-crimson);
}
.navbar-nav > li > a:hover{
  border-top: 0.1875rem solid #DC143C;
  border-top: 0.1875rem solid var(--primary-crimson);
}
.header-wraper{
  position: relative;
  /* <a href="http://www.freepik.com">Designed by starline / Freepik</a> */
  background: url(/find-amir-project/static/media/header.c9cf8857.jpg) no-repeat; 
  background-size: cover;
  background-position: center;
  height: 100vh;

}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: 'Share Tech Mono', monospace;
  z-index: 1;
}
/* .main-info h1{
  color: var(--primary-crimson);
  text-transform:uppercase;

  
} */

@media(max-width: 768px){
  .main-info h1{
    font-size: 2rem !important;
  }
  .typed-text {
    font-size: 2rem  !important;
  }
 
}


 /* typed text */
 .typed-text{
   font-size: 2rem;
   color:#fff;
   color:var(--primary-white);
  
   font-family: 'Share Tech Mono', monospace;


 }
 /* header github button */
.btn-github{
 border: 2px #DC143C;
 border: 2px var(--primary-crimson); 
border-radius: 12px;
background:#021933;
text-transform: uppercase;

padding: 0.625rem 0.75rem 0.75rem 0.75rem;
color: #fff;
color: var(--primary-white);
margin-top: 2rem;
font-family: 'Share Tech Mono', monospace;
}
.btn-github:hover{
 
  text-decoration: none;
  background-color:#DC143C;
  background-color:var(--primary-crimson);
  color: #fff;
  color: var(--primary-white);
  transition:  .2s ease-in-out;
}
.tsparticles-canvas-el{
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;

}

@media(max-width: 768px){
  .particles-canvas{
    display: none;

  }

}

/* about me */

.photo-wrap{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}


.profile-img{
  width: 12rem;
  border-radius: 50%;
  padding:  .3rem;
  border:  2px solid #021933;

}
.about-heading{
  color:#021933;
  text-transform: uppercase;
  font-family: 'Share Tech Mono', monospace;
}
.about-wrapper p{
  font-family: 'Share Tech Mono', monospace;
}

@media(max-width: 468px){
  .profile-img{
    width: 10rem;
  }
}

.btn-resume{
  border: 2px solid #021933;
text-transform: uppercase;
border-radius: 12px;
padding: 0.625rem 0.75rem 0.75rem 0.75rem;
color: #000000;
color: var(--primary-black);
margin-top: 2rem;
font-family: 'Share Tech Mono', monospace;

}

.btn-resume:hover{
  text-decoration: none;
  background-color:#DC143C;
  background-color:var(--primary-crimson);
  color: #fff;
  color: var(--primary-white);
  transition:  .2s ease-in-out;

}

/***** Services **** */
.services{
  text-align: center;
  padding-bottom: 2rem;
  background: #d3d3d3;
  background: var(--primary-light-grey);

}

.services h1{
  color:#021933;
  text-transform: uppercase;
  font-family: 'Share Tech Mono', monospace;
}

.services .circle{
  position: relative;
  margin: 0.375rem auto;
  
  background: #021933;
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;


}
.services .icon{
  font-size: 1.4rem;
  color: #fff;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);

}
.services .box{
  height: 16rem;
  border-bottom: 0.3125rem solid #001e24;
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #fff;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;

}
.services .box:hover{
  background:#DC143C;
  background:var(--primary-crimson);
  border-bottom: 0.3125rem solid #000000;
  border-bottom: 0.3125rem solid var(--primary-black);
}

.services .box:hover .icon{
  color: #000000;
  color: var(--primary-black);

}
.services .box:hover .icon{
  color: #000000;
  color: var(--primary-black);
  
}
.services .box:hover .circle{
 background: #fff;
 background: var(--primary-white);
  
}

.services .box:hover p{
  color: #fff;
  color: var(--primary-white);  
 }
 .services h3{
  font-family: 'Share Tech Mono', monospace;

}
.services p{
  font-family: 'Share Tech Mono', monospace;
}

 /***Media***/
 @media(max-width: 850px){
  .services h3{
    font-size: large;

  }
  .services p{
    font-size: small;
  }
  
    
  
  }


 /***Projects******/
 .project-wrapper{
   background:#F0F8FF;
   background:var(--primary-alicewhite);
   padding: 2rem 0 ;

 }
 .project-wrapper h1{
   color: #021933;
   font-family: 'Share Tech Mono', monospace;
 }
 .project-image{
   width: 15rem;
   border: 1px solid #021933;
   padding: 0;
 }


.project-image-box{
  position: relative;
  margin: 1rem;
  

}

/*****overflow-box---***/
.overflow{
  position: absolute;
  opacity:0;
  background: #000000;
  background: var(--primary-black);
  width: 15rem;
  
  top: 0;
  border:  1px solid #640000;
  border:  1px solid var(--primary-red);
  cursor: pointer;
}
.project-image-box:hover .overflow{
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.project-icon{
  position:absolute ;
  opacity: 1;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color:#DC143C;
  color:var(--primary-crimson);
  font-size: 1.5rem;

}
.project-image-box:hover .project-icon{
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;

}
/*---overflow box---*/
.project-image-popupbox{
  width: 35rem;
  padding: 0 0.5rem;
   
}
.hyper-link{
  cursor: pointer;
  color:green;
}
.hyper-link:hover {
 
  color:#DC143C;
 
  color:var(--primary-crimson);
}

/*media popupbox style*/

@media(max-width: 770px) {
  .project-image-popupbox{
    width: 80%;
  }
  

}
@media (max-height: 570px){
  .popupbox-wrapper{
    height:60%;
    width: 80%;

  }

  .project-image-popupbox{
    width: 30%;
  }
}


@media(max-height: 280px) {
  .popupbox-wrapper{
    height: 60% ;
    width: 50%;
  }
  .project-image-popupbox p{
    font-size: small;
  }

  .project-image-popupbox{
    display:none;

    width: 20%;
  }
  

}

 /***Contacts******/

 .contacts{
   background: #fff;
   background: var(--primary-white);
   padding: 2rem 0;
 }
 .contacts h1{
   color:#021933;
   padding: 1.3rem 0;
   text-transform: uppercase;
   font-family: 'Share Tech Mono', monospace;;

 }

 .contacts p{
   color: #303030;
   color: var(--primary-light-dark);
   font-size: 1.2rem;
   font-family: 'Share Tech Mono', monospace;
   
  
 }


 /* inputs */

 .contacts input, .contacts textarea{
   width: 100%;
   box-sizing: border-box;
   background: none;
   resize: none;
   border: 0;
   border-bottom: 0.125rem solid #808080;
   border-bottom: 0.125rem solid var(--primary-grey);
   border-radius: 0;
   color: #021933;
   outline:0 !important;
   box-shadow: none !important;
   margin-bottom: 1rem;
   padding: 0 !important;
   font-family: 'Share Tech Mono', monospace;
 }
 .contacts .container{
   padding: 4rem 0;

 }

 .contacts input:focus, .contacts textarea:focus{
   background: transparent;
   color: #021933;
   border-bottom: 0.125rem solid #808080;
   border-bottom: 0.125rem solid var(--primary-grey); 
 } 

 .contacts textarea{
   height: 12.2rem !important;
   

 }

 .contact-btn {
   margin: 1rem 0 0 0;
   width: 100%;
   background: none;
   color: #000000;
   color: var(--primary-black);
   border: 2px solid #021933;
   text-transform: uppercase;
   border-radius: 12px;
   margin-top: 2rem;
   font-family: 'Share Tech Mono', monospace;

 }

 .contact-btn:hover{
   background: #DC143C;
   background: var(--primary-crimson);
   color: #F0F8FF;
   color: var(--primary-alicewhite);
 }
 .line{
   position: relative;
   top: -0.8rem;
   width: 0;
   height: 0.125rem;
   background: #DC143C;
   background: var(--primary-crimson);
   display: inline-block;

 }
 input:focus+.line, textarea:focus+.line{
   width: 100%;
   transition: width 0.5s ease-in-out;
 }
 input.form-control{
   margin: -0.3rem 0;
 }
 textarea.form-control{
  margin: -0.3rem 0;

 }

 .error-message{
   color: #DC143C;
   color: var(--primary-crimson);
   background: none ;
   font-family: 'Share Tech Mono', monospace;
 }
 .success-message{
   font-weight: 400;
   color: green;
   color: var(--primary-green) ;
   font-family: 'Share Tech Mono', monospace;
 }

 /*--------Footer Component--------*/
 .footer{
   background: #000000;
   background: var(--primary-black);
   color: #808080;
   color: var(--primary-grey);
   padding: 3rem;
   font-family: 'Share Tech Mono', monospace;

 }
 .footer a{
   text-decoration: none;
   color: #808080;
   color: var(--primary-grey);
   font-family: 'Share Tech Mono', monospace;
 }
 .footer a:hover{
   text-decoration: none;
   color: #DC143C;
   color: var(--primary-crimson);
   cursor: pointer;
 }
 .footer p{
   margin-bottom: 0;
   font-family: 'Share Tech Mono', monospace;
 }
 a.social {
  margin: 0 1rem;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  display: inline-block;
}
a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}
a.linkedin{
  color:#2867B2;
}
a.git{
  color: #4078c0;
}
a.Reddit {
  color:#FF4500 ;
}

 /*--------Media--------*/

 @media(max-width: 768px){
   .contacts{
   
     overflow-x: hidden;
   }
   .contacts .container{
     
     padding: 0 1rem;
   }
   .contacts p{
     

     padding: 0 0.4rem;

   }
 }
